import React, {FunctionComponent, MutableRefObject, RefObject, useEffect, useRef, useState} from "react"
import "./style.css"
import Level from "./level";

export const isMobile = (): boolean => !!(typeof window !== "undefined" && navigator.userAgent.match(/(iPad)|(iPhone)|(iPod)|(android)|(webOS)/i))

export interface VideoType {
  src: string,
  poster: string,
  id: string,
  hoverVideo: string,
}

const Video: FunctionComponent<{ video: VideoType, preload?: string, showRibbon?: boolean }> = ({video, preload = "none", showRibbon = false}) => {

  const videoRef = useRef();

  return <div className={"video_wrapper"}>
    <VideoRibbon project={video} visible={showRibbon}>
      <IconOverlay ref={videoRef}>
        <DynamicVideo ref={videoRef} src={video.src}>
          <HoverPreview ref={videoRef} src={video.hoverVideo} poster={video.poster}>
            <FormKeeper>
              <PlaceHolder poster={video.poster}>
                <LightBox ref={videoRef}>

                  <video
                    ref={videoRef as any}
                    poster={video.poster}
                    controls={isMobile()}
                    preload={preload || "none"}
                    style={{
                      'gridRow': '1',
                      'gridColumn': '1',
                      "width": "100%",
                      "height": "auto"
                    }}>
                    <source
                      id={video.id + "_src"}
                      src={video.src}
                      type="video/mp4"/>
                  </video>

                </LightBox>
              </PlaceHolder>
            </FormKeeper>
          </HoverPreview>
        </DynamicVideo>
      </IconOverlay>
    </VideoRibbon>
  </div>
}

const LightBox = React.forwardRef<any, any>(({children}, ref: any) => {

  const [playing, setPlaying] = useState(false);

  useEffect(() => ref.current.addEventListener("playing", () => setPlaying(!isMobile())), [ref])

  return <Level className={`newLightBox ${playing ? 'lightRunning' : ''}`}>
    <div className={"newLightBoxChild"}>

      <p onClick={() => setTimeout(() => {
        ref.current.pause();
        setPlaying(false);
      }, 100)} className={"closeicon"}>✕</p>
      {children}
    </div>

  </Level>
});

const PlaceHolder: FunctionComponent<{ poster?: any }> = ({poster, children}) => {
  return <Level ghost={true}>
    <img alt="placeholder video poster"
         className={"placeholdervid"}
         loading="lazy"
         src={poster}/>
    {children}
  </Level>
}

const VideoRibbon: FunctionComponent<{ project?: any, visible: boolean }> = ({project, visible, children}) => {
  return <Level ghost={true}>
    {visible && <div className={"videoRibbon"}>
      <p>{project.style === 1 ? 'Budget' : (project.style === 2 ? 'Premium' : project.style === 3 ? 'Enterprise' : 'Real')}</p>
    </div>}
    {children}
  </Level>
}

const FormKeeper: FunctionComponent<{}> = ({children}) => {
  return <Level ghost={true}>
    <img alt="placeholder videobakers banner"
         loading="lazy"
         src={"https://beta.videobakers.de/static/360_203-b9cd43a99b72f1ff3f133651b2638c31.jpg"}/>
    {children}
  </Level>
}


// @ts-ignore
const HoverPreview = React.forwardRef<any, any>(({children, poster, src}, ref: any) => {
  const [hover, setHover] = useState(false);
  // @ts-ignore
  return <Level className={"hoverLevel"} onMouseOver={() => setHover(true)}
                onClick={() => {
                  ref.current.play();
                  var event = document.createEvent("HTMLEvents");
                  event.initEvent("playclick", true, true);
                  // @ts-ignore
                  event.eventName = "playclick";
                  ref.current.dispatchEvent(event);
                }}
                onMouseLeave={() => setHover(false)}>


    <video className={"hoverVideo"} poster={poster} onCanPlay={() => setHover(true)} autoPlay={hover}
           key={`${hover}`}
           preload={"auto"} loop={true} muted={true}>
      <source src={src} type="video/mp4"/>
    </video>


    {children}
  </Level>
});


const DynamicVideo = React.forwardRef<any, any>((props, ref: any) => {

  return <Level>
    {props.children}
  </Level>;
});


const IconOverlay = React.forwardRef<any, any>((props, ref: any) => {
  // @ts-ignore
  var {children, className} = props;
  const [curState, setCurState] = useState("initial");

  useEffect(() => {
    ref.current.addEventListener("playclick", () => setCurState("loading"), false);
    ref.current.addEventListener("loadstart", () => setCurState("loadingdate"), false);
    ref.current.addEventListener("canplay", () => setCurState("initial"), false);
  }, [ref])

  return <Level {...props} className={`iconOverlay
    ${curState === 'loadingdate' ? 'isLoadingData' : ''}
    ${curState === 'loading' ? 'isLoading' : ''}`}>
    <div className={`iconOverlayContent ${className}`}>
      {children}
    </div>
    <img alt="play video icon" className={"iconOverlayPlay"} src={require('./pla.svg')}/>
    <img alt="play video icon" className={"iconOverlayPlay"} src={require('./pla.svg')}/>
  </Level>;
});

export default Video
