import * as style from './style.module.scss';
import Button, {ButtonMode, ButtonSize} from "../button";
import {PriceModes} from "../filtergrid";
import React, {FunctionComponent, useEffect, useState} from 'react';
import {isMobile} from "../video/video";


const Select: FunctionComponent<{ className?: string, options: string[], value: string, setValue: any }> = ({
                                                                                                              className,
                                                                                                              value, setValue,
                                                                                                              options
                                                                                                            }) => {
  return (
    <div className={`${className} ${style.selectWrapper}`}>
      {isMobile() && <select onChange={e => console.log(e)} className={style.select}>
        <option value="" selected disabled hidden>Preisklasse wählen</option>
        {options.map(option => <option  value={option}>{option}</option>)}
      </select>}
      {!isMobile() && options.map(option => {
        return <Button className={style.selectButton} buttonMode={value === option ? ButtonMode.NORMAL : ButtonMode.BLANK}
                       buttonSize={ButtonSize.MEDIUM} onClick={() => setValue(option)}>{option}</Button>
      })}
    </div>
  )
}

export default Select;
